.Card {
    text-align: center;
    background-color: rgba(238, 238, 238,0.5);
    padding: 20px;
    border-radius: 15px;
    margin: 5px;
    word-break: break-all;
    flex: 1 0 300px;
    max-width: 300px;
    transition: all 0.5s ease-in-out;
}
.Card:hover {
    box-shadow: 0 0 3px 3px #ccc; 

}